import React from 'react';
import styled from 'styled-components';

import { breakpoints } from '../constants';
import { SpacingKey, spacingValues } from '../types';

interface StackProps {
    direction: Record<string, 'row' | 'column'>;
    spacing: Record<string, number>;
    children: React.ReactNode;
}

const BaseStack = styled.div<StackProps>`
    display: flex;
    gap: ${(props) =>
        spacingValues['bp-xsmall'] * (props.spacing['bp-xsmall'] || 0)}px;
    flex-direction: ${(props) => props.direction.xsmall || 'column'};

    ${(props) =>
        Object.entries(breakpoints)
            .map(([key, value]) => {
                // Ensure the key is treated as a SpacingKey
                const spacingKey = `bp-${key}` as SpacingKey;
                const directionValue = props.direction[key];
                const spacingValue = props.spacing[spacingKey];
                return `
      @media (min-width: ${value}px) {
        flex-direction: ${directionValue || 'column'};
        gap: ${(spacingValue || 0) * (spacingValues[spacingKey] || 0)}px;
      }
    `;
            })
            .join('')}
`;

export const Stack: React.FC<StackProps> = ({
    direction,
    spacing,
    children,
}) => {
    return (
        <BaseStack direction={direction} spacing={spacing}>
            {children}
        </BaseStack>
    );
};
